import React, { useState } from 'react';
import { Link, useNavigate } from 'react-router-dom';
import { useTranslation } from 'react-i18next';
import { Helmet } from 'react-helmet-async';
import { useDispatch, useSelector } from 'react-redux';
import { login, selectAuthLoading, selectAuthError } from '../../store/slices/authSlice';
import { AppDispatch } from '../../store/types';

const LoginPage: React.FC = () => {
  const { t } = useTranslation();
  const dispatch = useDispatch<AppDispatch>();
  const navigate = useNavigate();
  
  const loading = useSelector(selectAuthLoading);
  const error = useSelector(selectAuthError);
  
  const [formData, setFormData] = useState({
    email: '',
    password: '',
    remember: false
  });

  const handleChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value, type, checked } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: type === 'checkbox' ? checked : value
    }));
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    try {
      await dispatch(login({
        email: formData.email,
        password: formData.password
      })).unwrap();
      
      navigate('/profile');
    } catch (error) {
      console.error('Login failed:', error);
    }
  };

  return (
    <div className="login-page">
      <Helmet>
        <title>{t('login.pageTitle')} | Mart.az</title>
        <meta name="description" content={t('login.metaDescription')} />
      </Helmet>

      <div className="login-container">
        <div className="auth-form-container">
          <h1 className="auth-title">{t('login.title')}</h1>
          <p className="auth-subtitle">{t('login.subtitle')}</p>

          {error && (
            <div className="alert alert-danger" role="alert">
              {error}
            </div>
          )}

          <form onSubmit={handleSubmit} className="auth-form">
            <div className="form-group">
              <label htmlFor="email">{t('login.emailLabel')}</label>
              <input
                type="email"
                id="email"
                name="email"
                value={formData.email}
                onChange={handleChange}
                required
                placeholder={t('login.emailPlaceholder')}
                className="form-control"
              />
            </div>

            <div className="form-group">
              <label htmlFor="password">{t('login.passwordLabel')}</label>
              <input
                type="password"
                id="password"
                name="password"
                value={formData.password}
                onChange={handleChange}
                required
                placeholder={t('login.passwordPlaceholder')}
                className="form-control"
              />
            </div>

            <div className="form-extras">
              <div className="remember-me">
                <input
                  type="checkbox"
                  id="remember"
                  name="remember"
                  checked={formData.remember}
                  onChange={handleChange}
                />
                <label htmlFor="remember">{t('login.rememberMe')}</label>
              </div>
              <Link to="/forgot-password" className="forgot-password-link">
                {t('login.forgotPassword')}
              </Link>
            </div>

            <button 
              type="submit" 
              className="btn btn-primary btn-block"
              disabled={loading}
            >
              {loading ? t('common.loading') : t('login.loginButton')}
            </button>
          </form>

          <div className="auth-footer">
            <p>
              {t('login.noAccount')}{' '}
              <Link to="/register" className="auth-link">
                {t('login.registerNow')}
              </Link>
            </p>
          </div>
        </div>

        <div className="auth-benefits">
          <h2>{t('login.benefitsTitle')}</h2>
          <ul>
            <li>{t('login.benefit1')}</li>
            <li>{t('login.benefit2')}</li>
            <li>{t('login.benefit3')}</li>
            <li>{t('login.benefit4')}</li>
          </ul>
        </div>
      </div>
    </div>
  );
};

export default LoginPage; 